@mixin span_font($font: 16px) {
    font-size: $font;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
}

a.back {
    float: left;
    padding: 8px 16px;
    border-radius: 8px;
    font-size: 14px;
    margin-bottom: 16px;
    font-weight: 500;
    font-family: 'Inter', sans-serif;

    img {
        margin-right: 10px;
        vertical-align: middle;

    }
}

.user_data {
    height: 218px;
    padding: 20px;
   
    width: 100%;
    display: flex;
}

.user_data_personal {
    width: calc(100% - 200px);
    display: flex;
    font-family: 'Inter', sans-serif;
}

.user_data_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 178px;
    height: 178px;
    border-radius: 8px;
    margin-right: 20px;
}

.user_data_text {
    text-align: left;
    display: flex;
    width: 70%;
    flex-direction: column;
    justify-content: space-between;

    div {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        h2 {
            margin: 0;
            text-transform: uppercase;
            font-size: 18px;
            font-family: 'Inter', sans-serif;
            font-weight: 700;
        }

        img {
            margin-left: 8px;
        }

        a {
            color: #3D4EEA;
        }

        p {
            margin: 0;
        }

        span {
            font-size: 14px;
            margin-right: 5%;
            line-height: 21px;
            color: inherit;
            display: inline-block;
            width: 25%;
        }

        p:last-of-type {
            span {
                @include span_font();
            }
        }
    }
}

.user_review {
    @include span_font(18px);
    text-align: left;
    margin: 20px;
}

.user_data_buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    button,
    a {
        width: 196px;
        @include span_font(16px);
        border-radius: 4px;
        box-sizing: border-box;
        padding: 8px 12px;
        line-height: normal;
        letter-spacing: 0.8px;
        border-width: 1px;
        border-style: solid;
    }
}

.user_review_total {    
    height: 174px;
    padding: 20px;
    display: flex;   
    border-radius: 8px;   
    flex-direction: column;
    justify-content: space-between;
    text-align: left;

    p {
        margin: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        b {
            @include span_font('16px');
            line-height: normal;
            letter-spacing: 0.8px;
        }

        span:last-of-type {
            font-size: 14px;
            font-weight: 500;
            font-family: 'Inter', sans-serif;
        }

    }

    .review_text {
        font-size: 12px;
        line-height: 18px;
        font-weight: 400;
        text-align: left;
        height: 50px;
        margin: 12px 0;
        white-space: wrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    h5 {
        margin: 0;
        cursor: pointer;
    }

    a,
    h5 {
        color: #3D4EEA;
        font-family: 'Inter', sans-serif;

        img {
            margin-left: 5px;
        }
    }
}

.main_user_review {
    display: grid;
    grid-template-columns: 33% 33% auto ;
    gap: 10px;
    height: 50vh;
    overflow: auto;
}

.changerating {
    width: calc(100% - 40px);
    margin: 0 auto;
    padding: 16px;
    display: flex;
    font-family: Inter;
    font-style: normal;
    border-radius: 8px;
    justify-content: space-between;
    align-items: center;

    input {
        width: calc(100% - 220px);
        padding: 10px;
        color: #808080;
        font-size: 16px;
        font-weight: 500;
    }

    span {
        @include span_font(18px);
    }
}