body::-webkit-scrollbar {
    width: 0;
    /* ширина всей полосы прокрутки */
}

body::-webkit-scrollbar-track {
    background: #fff;
    /* цвет зоны отслеживания */
}

body::-webkit-scrollbar-thumb {
    background-color: inherit;
    /* цвет бегунка */
    border-radius: 20px;
    /* округлось бегунка */
    border: 3px solid #ECEEFD;
    /* отступ вокруг бегунка */
}

.dialogs {
    grid-template-columns: 147px auto 108px;
}

.all_dialogs {
    display: flex;
    justify-content: space-between;
    margin-top: 12px;
    height: 77vh;
}

.dialogs_users {
    width: max(250px, 348px);
    max-height: 80vh;
    overflow: auto;
    padding-right: 3px;

    &::-webkit-scrollbar {
        width: 5px;
    }


    &::-webkit-scrollbar-track {
        background: #f1f1f1;
    }


    &::-webkit-scrollbar-thumb {
        background: #888;
        border-radius: 5px;
    }


    &::-webkit-scrollbar-thumb:hover {
        background: #555;
    }
}

.select_user_dialog {
    width: calc(100% - 355px);
    border-left: 2px solid #F5F5F5;
}

.dialog_user {
    padding: 8px;
    border-radius: 8px;
    padding: 8px;
    z-index: 100;
    position: relative;
    margin-bottom: 10px;   
    cursor: pointer;
    p {
        margin: 0;
        display: flex;
        color: inherit;
        justify-content: space-between;

        b {
            font-size: 16px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.8px;
            a {
                margin-left: 10px;
                font-size: 13px;
                color: #3D4EEA;
                font-family: 'Open Sans', sans-serif;
            }
           
        }
    }
    p.sendler_link {
        justify-content: flex-start;
        align-items: baseline;
        a {
            margin-left: 10px;
            font-size: 13px;
            color: #3D4EEA;
            &::hower {
                text-decoration: underline;
            }
        }
      
    }
}


.dialog_text {
    width: 60%;
    border-radius: 8px;
    margin: 0 0 12px 10px;
    padding: 20px;

    p {
        margin-bottom: 16px;
    }

    p:last-of-type {
        margin: 0;
        font-family: 'Rubik', sans-serif;
        font-size: 12px;
        justify-content: flex-start;
        a {
            color: #3D4EEA;
        }
        
        span {
            margin-right: 10px;
        }
    }
    p.link {
        display: block;
        text-align: left;
        span:last-of-type {
            display: block;
        }
    }
    
}

.text_message {
    display: flex;
    position: absolute;
    font-family: 'Inter', sans-serif;
    height: 118px;
    bottom: 0;
    width: calc(100% - 357px);
    align-items: flex-start;
    
    justify-content: space-between;
    border-top: 2px solid #F5F5F5;

    input {
        width: calc(100% - 126px);
        border: none;
        outline: none;
        display: inline-block;
        padding: 26px;
        font-size: 16px;
    }

    button {
        width: 100px;
        height: 28px;
        margin: 84px 26px 0 0;
        font-weight: 700;
    }
}
.sendler {
    text-transform: capitalize;
}
.sendler_link {
    text-align: left;
}
.all_dialog_wrapper {
    overflow-y: auto;
    height: 60vh;

    &::-webkit-scrollbar {
        width: 7px;
        background-color: #F5F5F5;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #18aaaa;
    }
}

.all_dialog {
    height: auto;

}
.new_message::after {
    content: '.';
    border-radius: 20px;
    margin: 0 0 0 -15px;
    line-height: 0px;
    display: inline-block;
    font-size: 13px;
    transform: translate(0px, 25px);
    color: red;
    padding: 8px 6.3px;
    background-color: red;
}