.main_modal {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: rgba(64, 64, 64, 0.60);
    backdrop-filter: blur(10px);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    .modal {
        display: flex;
        width: 720px;        
        flex-direction: column;
        padding: 40px 20px;
        align-items: center;
        gap: 40px;
        border-radius: 8px;
        justify-content: space-between;

        p, h4 {
            margin: 0;
            font-size: 18px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            width: 80%;
            text-transform: uppercase;
        }
        h4 {
            color: #3D4EEA;
        }

        button {
            font-size: 18px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            margin-left: 10px;
            padding: 10px 20px;
            
        }

    }
}