.main_photo {
    width: calc(82% - 40px);
    position: relative;
}

.services {
    text-align: left;

    p {
        font-size: 14px;
        display: inline-block;
        font-family: Inter;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        padding: 4px 8px;
        border-width: 1px;
        cursor: pointer;
        border-style: solid;
        margin: 0 10px 10px 0;
        border-radius: 4px;
        text-transform: capitalize;

    }

}

.all_images_wrap {
    height: 78vh;
    overflow-y: auto;
    margin-top: 24px;
}

.all_images {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    gap: 10px 12px;
    div {
        height: 200px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 8px;
        cursor: pointer;
    }
}