.main_modal {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: rgba(64, 64, 64, 0.60);
    backdrop-filter: blur(10px);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    

    .modal_review {
        display: flex;
        width: 720px;
        flex-direction: column;
        padding: 20px;
        align-items: center;
        gap: 20px;
        border-radius: 8px;
        justify-content: space-between;
        .modal_block {
            width: 100%;
            text-align: left;
            p,
            h4 {
                margin: 0;
                font-size: 18px;
                font-family: Inter;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                width: 100%;
                display: flex;
                justify-content: space-between;
                img {
                    border: 6px solid #E2E2E2;
                    border-radius: 50%;
                    background-color: #E2E2E2;
                    cursor: pointer;
                }
            }
            a {
                img {
                    margin-left: 8px;
                }
            }
        }    

        h4 {
            color: #3D4EEA;
        }

        button {
            font-size: 18px;
            font-family: Inter;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            margin-left: 10px;
            padding: 10px 20px;

        }

        span {
            display: block;
            font-size: 12px;
            font-family: Rubik;
            font-style: normal;
            width: 100%;
            font-weight: 400;
            line-height: 17px;
            text-align: left;
        }

    }
}