.main_message {
    width: calc(82% - 40px);
    height: 336px;
    padding: 16px;
    border-radius: 8px;
    text-align: left;

    h2 {
        font-size: 18px;
        font-weight: 700;
        font-family: 'Inter', sans-serif;
        text-align: left;
        margin: 0;
    }

    textarea {
        display: block;
        height: 200px;
        padding: 10px;
        border: none;
        margin: 20px 0;
        width: 100%;
        border-radius: 8px;
        outline: none;
    }

    button {
        padding: 10px 20px;
        margin-right: 20px;
        font-size: 18px;
        font-family: Inter;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }
    .one_message {
        padding: 16px;
        position: relative;
        left: -16px;
        width: calc(100% + 32px);
        border-radius: 8px;
    }
    .message_header {
        display: flex;
        justify-content: flex-start;
        margin: 12px 0 0px;
        span {
            display: inline-block;
            color: inherit;
            width: 20%;        
            text-align: left;
            font-size: 14px;
        }
    }
}