.main_history {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: rgba(64, 64, 64, 0.60);
   
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    


    .modal_history {
        height: 80vh;
        margin-top: 127px;
        width: 720px;  
         
        padding: 20px;       
        border-radius: 8px;
        .modal_block {
            width: 100%;
            text-align: left;
            margin-bottom: 20px;

            p,
            h4 {
                margin: 0;
                font-size: 18px;
                font-family: Inter;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                width: 100%;
                display: flex;
                justify-content: space-between;

                img {
                    border: 6px solid #E2E2E2;
                    border-radius: 50%;
                    background-color: #E2E2E2;
                    cursor: pointer;
                }
            }

            a {
                img {
                    margin-left: 8px;
                }
            }
           
        }
        .history_data {
            overflow-y: auto;  
            height: 70vh; 
        }
        .enter {
            display: flex;
            justify-content: space-between;
            width: 100%;
            margin: 0 0 10px 0;
            padding: 8px 12px;
            border-radius: 4px;
            span {
                display: block;
                font-size: 16px;
                font-family: Inter;
                font-style: normal;
                font-weight: 700;                
                letter-spacing: 0.8px;                
                text-align: center;
                display: inline-block;
                width: 30%;
            }

            span:first-of-type {
                text-align: left;
                font-size: 14px;               
                font-weight: 500;
                
            }

            span:last-of-type {
                text-align: right;
            }
        }

    }
}