.modal_image {
    display: flex;
    height: 525px;
    justify-content: space-between;
    padding: 20px;
    border-radius: 8px;
    width: 720px;

}

.master_image {
    width: 300px;
    margin-right: 20px;
    height: auto;
    border-radius: 8px;
    object-fit: contain;
}

.master_data {
    height: 485px;
    position: relative;
    width: calc(100% - 320px);
    text-align: left;
    font-family: Inter;
    font-style: normal;
    p {
        display: flex;
        justify-content: space-between;
        color: inherit;
        font-size: 16px;
        font-family: Inter;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.8px;
        margin: 5px 0;

        img {
            cursor: pointer;
            width: 18px;
            height: 18px;
            padding: 4px;
            justify-content: center;
            align-items: center;
            background-color: #353D6A;
            border-radius: 50%;
        }
    }
    a {
        font-weight: 500;
        img {
            margin-left: 10px;
        }
    }

    h3 {
        font-size: 14px;
        font-family: Inter;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        border-radius: 4px;
        width: fit-content;
        padding: 4px 8px;
        text-transform: capitalize;
    }
    h4 {
        font-weight: 500;
    }

    button {
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 10px 20px;
        font-size: 18px;
        font-family: Inter;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }

}